/* NavBar */
div,
h2,
h3,
h4,
p {
  font-weight: 300;
}
.headpadding {
  width: 100%;
  height: 70px;
}
.centered2 {
  position: absolute;
  text-align: center;
  margin: auto;
  /* padding-top: 60px; */
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
}

.nav-scrolled {
  background-color: rgb(0, 0, 0, 1);
  width: 100%;
  display: flex;
  margin: 0;
  padding: 0;
  z-index: 10005;
  position: fixed;
  top: 0;
  text-align: center;
  margin: 0 auto;
  align-content: center;
  align-items: center;
  align-self: center;
  justify-content: center;
  z-index: 10;
  height: 70px;
}
.nav {
  background-color: black;
  width: 100%;
  display: flex;
  margin: 0;
  padding: 0;
  z-index: 10005;
  opacity: 0.9;
  text-align: center;
  margin: auto;
  align-content: center;
  align-items: center;
  align-self: center;
  justify-content: center;
  z-index: 100;
  height: 70px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.link {
  text-decoration: none;
  font-weight: 300;
  color: white;
  width: 150px;
  height: 30px;
  padding: 20px 0px 20px 0px;
  text-align: center;
  font-size: 20px;
  -webkit-transition: 0.5s;
  transition: 0.5s;

}
.link:hover {
  color: black;
  background-color: white;

  cursor: -webkit-grab;

  cursor: grab;
}
.active {
  color: black;
  background-color: white;
  cursor: -webkit-grab;
  cursor: grab;
}

@media only screen and (max-width: 800px) {
  .nav {
    display: none;
    -webkit-transition: 0.2s;
    transition: 0.2s;
  }
  .nav-scrolled {
    background-color: black;
    width: 60px;
    height: 240px;
    display: flex;
    flex-direction: column;
    left: 10px;
    top: 25%;
    position: fixed;
    z-index: 10005;
    opacity: 0.9;
    text-align: center;
    align-content: center;
    align-items: center;
    align-self: center;
    justify-content: center;
    overflow: hidden;
    box-shadow: 7px 7px 0px 0px rgb(0, 0, 0, 0.5);
    z-index: 10;
  }
  .link {
    text-decoration: none;
    font-weight: 300;
    color: white;
    /* width: 60px; */
    height: 10px;
    padding: 10px 0px 10px 0px;
    left: 0%;
    text-align: center;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    font-size: 9px;

    overflow: hidden;
  }
  /* .link:hover {
    color: black;
    font-weight: bold;
